	module.exports = {"data":{"markdownRemark":{"html":"<p>魔理沙はとにかく何に乗せようか迷った。魔理沙に二輪は似合わないわけではない、魔理沙はどんな種類のバイクに乗せても絵になってしまう。アメリカンでも似合うし、ネイキッドでも似合うし、ビッグスクーターでも似合うし…。 結局、彼女はHONDAのNS-1に乗せてみた。50ccの原付ではあるが、改造しだいでは軽く120km/hすら超えてしまう名車だ。魔理沙は努力家と言う設定だし、下手に大排気量の二輪に乗せるより似合ってるのではないだろうか。何もしなくても十分強い霊夢に対して、魔理沙は努力で追随しているわけだし。</p>","frontmatter":{"cover":null,"date":"2004-10-02T00:00:00+09:00","strDate":"2004/10/1","title":"NS-1&魔理沙"}}},"pathContext":{"path":"/ja/blog/2004/10/02-ns-1魔理沙/","langKey":"ja"}}

